<template>
  <div style="margin:25px 30px;min-width: 1000px;">
      <!-- 标签页 -->
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
            <el-tab-pane label="项目信息" name="first">
                <!-- 信息表格渲染 webAccess-->
              <!-- <button @click="Fnviewproject">获取样式表</button> -->
              <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                  <el-form-item label="项目名称" prop="p_name">
                      <el-input v-model="ruleForm.p_name"></el-input>
                  </el-form-item>
                  <el-form-item label="画面地址" prop="p_dashboard">
                      <el-input v-model="ruleForm.p_dashboard"></el-input>
                  </el-form-item>
                  <el-form-item label="省" prop="p_province">
                      <el-input v-model="ruleForm.p_province"></el-input>
                  </el-form-item>
                  <el-form-item label="市" prop="p_city">
                      <el-input v-model="ruleForm.p_city"></el-input>
                  </el-form-item>
                  <el-form-item label="甲方单位" prop="p_party_a">
                      <el-input v-model="ruleForm.p_party_a"></el-input>
                  </el-form-item>
                  <el-form-item label="采购日期" prop="p_purchase_time">
                      <el-date-picker
                      v-model="ruleForm.p_purchase_time"
                      type="date"
                      format="yyyy 年 MM 月 dd 日"
                      value-format="yyyy-MM-dd"
                      placeholder="选择日期">
                      </el-date-picker>
                  </el-form-item>
                  <el-form-item label="竣工日期" prop="p_built_time">
                      <el-date-picker
                      v-model="ruleForm.p_built_time"
                      type="date"
                      format="yyyy 年 MM 月 dd 日"
                      value-format="yyyy-MM-dd"
                      placeholder="选择日期">
                      </el-date-picker>
                  </el-form-item>
                  <el-form-item label="联系人姓名" prop="p_contact_name">
                      <el-input v-model="ruleForm.p_contact_name"></el-input>
                  </el-form-item>
                  <el-form-item label="联系人的电话" prop="p_contact_number">
                      <el-input v-model="ruleForm.p_contact_number"></el-input>
                  </el-form-item>
                  <el-form-item label="客户类型" prop="p_customer_type">
                      <el-input v-model="ruleForm.p_customer_type"></el-input>
                  </el-form-item>
                  <el-form-item label="项目类型" prop="p_type">
                      <el-input v-model="ruleForm.p_type"></el-input>
                  </el-form-item>
                  <el-form-item label="地址" prop="p_address">
                      <el-input v-model="ruleForm.p_address"></el-input>
                  </el-form-item>
                  <el-form-item label="安装时间" prop="p_setup_time">
                      <el-date-picker
                      v-model="ruleForm.p_setup_time"
                      type="date"
                      format="yyyy 年 MM 月 dd 日"
                      value-format="yyyy-MM-dd"
                      placeholder="选择日期">
                      </el-date-picker>
                  </el-form-item>
                  <el-form-item label="项目简介" prop="p_description">
                      <el-input type="textarea" v-model="ruleForm.p_description"></el-input>
                  </el-form-item>
                  <el-form-item>
                      <el-button type="primary" @click="Fneditproject('ruleForm')">修改</el-button>
                  </el-form-item>
              </el-form>
               <el-button type="danger" @click="Fndeactivateproject">删除项目</el-button>
            </el-tab-pane>
            <el-tab-pane label="画面" name="second">
              <iframe :src="ruleForm.p_dashboard" frameborder="0" scrolling="no" width="1200" height="750"></iframe>
            </el-tab-pane>
            <el-tab-pane label="采集设备" name="fourth">
              <div class="fromBox">
                <el-form label-position="right" :rules="rulesA" label-width="100px" ref="formLabelAlign" :model="formLabelAlign">
                    <el-form-item label="采集设备id" prop="cd_nodeid">
                        <el-input v-model.number="formLabelAlign.cd_nodeid"></el-input>
                    </el-form-item>
                    <el-form-item label="设备名称" prop="cd_name">
                        <el-input v-model="formLabelAlign.cd_name"></el-input>
                    </el-form-item>
                    <el-form-item label="出场日期" prop="cd_produce_time">
                        <el-input v-model="formLabelAlign.cd_produce_time"></el-input>
                    </el-form-item>
                    <el-form-item label="设备生产商" prop="cd_manufacturer">
                        <el-input v-model="formLabelAlign.cd_manufacturer"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="submitForm('formLabelAlign')">{{ISfromBox ?'修改':'添加'}}</el-button>
                        <el-button type="danger" v-show="ISfromBox" @click="Fndelcollectdevice()">删除</el-button>
                    </el-form-item>
                </el-form>
              </div>
            </el-tab-pane>
            <el-tab-pane label="关联的员工" name="shezhi">
              <router-view/>
            </el-tab-pane>
        </el-tabs>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
  export default {
    data() {
      return {
        activeName: 'first',
        value:false,//数据4-自动更新
        formLabelAlign: {
            cd_nodeid: '',
            cd_name: '',
            cd_produce_time: '',
            cd_manufacturer: '',
        },
        rulesA: {
          cd_nodeid: [
            { required: true, message: '请输入设备id', trigger: 'blur' },
            { type:'number', message: '请输入数字', trigger: 'blur' }
          ],
          cd_name: [
            { required: true, message: '请输入设备名称', trigger: 'blur' }
          ],
          cd_produce_time: [
            { required: true, message: '请输入设备出场日期', trigger: 'blur' }
          ],
          cd_manufacturer: [
            { required: true, message: '请输入设备生产商', trigger: 'blur' }
          ],
        },
        ISfromBox:true,
        ruleForm: {//表单提交信息
            "p_name": "",
            "p_dashboard": "",
            "p_description": "",
            "p_type": "",
            "p_customer_type": "",
            "p_party_a": "",
            "p_purchase_time": "",
            "p_setup_time": "",
            "p_built_time": "",
            "p_contact_name": "",
            "p_contact_number": "",
            "p_province": "",
            "p_city": "",
            "p_address": "",
 
        },
        rules: {//表单信息验证
          p_name: [
            { required: true, message: '请输入项目名称', trigger: 'blur' }
          ],
          p_dashboard: [
            { required: true, message: '请输入组态画面地址', trigger: 'blur' },
            { type:'url', message: '请输入url地址', trigger: 'blur' }
          ],
          p_type: [
            { required: true, message: '请输入项目类型', trigger: 'blur' }
          ],
          p_party_a: [
            { required: true, message: '请输入甲方单位', trigger: 'blur' }
          ],
          p_province: [
            { required: true, message: '请输入省份', trigger: 'blur' }
          ],
          p_city: [
            { required: true, message: '请输入城市', trigger: 'blur' }
          ],
          p_address: [
            { required: true, message: '请输入地址', trigger: 'blur' }
          ],
        },
      };
    },
    methods: {
      handleClick(tab) {//标签页
        // console.log(tab.label,event);
        let _this=this
        if(tab.label=='关联的员工'){
          _this.$router.push({
              path:'/bkIndex/bkseeProjectManagement/signproject', //也可以在路径上拼接
              query:{
                  company_id:this.$route.query.company_id,      //传递的值
                  project_id:this.$route.query.project_id
              }
          })
        }else {
          // _this.$router.push({
          //     path:'/bkIndex/bkseeProjectManagement/applycollectdevice', //也可以在路径上拼接
          //     query:{
          //         company_id:this.$route.query.company_id,      //传递的值
          //         project_id:this.this.$route.query.project_id,
          //         type:'修改'
          //     }
          // })
        }
      },
      onSubmit(){//数据4--表单查询事件
        console.log('查询表单提交事件')
      },
      submitForm(formName){//采集设备修改
        this.$refs[formName].validate((valid) => {
          if (valid) {
              this.formLabelAlign.company_id=this.$route.query.company_id
              this.formLabelAlign.project_id=this.$route.query.project_id
              this.formLabelAlign.user_id=this.buserInfo.user_id
              if(this.ISfromBox){
                url.editcollectdevice(this.formLabelAlign)
                .then(res=>{
                  console.log(res,'------------------修改-----------------')
                  this.Fncollectdevicelist()//采集列表
                  if(res.data.code){
                    this.$message({ 
                      message: res.data.message[0],
                      type: 'success'
                    });
                  }else{
                    this.$message({ 
                      message: '修改失败，请检查信息重新修改',
                      type: 'error'
                    });
                  }
                })
              }else{
                url.applycollectdevice(this.formLabelAlign).then(res=>{
                  console.log(res,'--------------添加')
                  this.Fncollectdevicelist()//采集列表
                  if(res.data.code){
                    this.$message({ 
                      message: res.data.message[0],
                      type: 'success'
                    });
                  }else{
                    this.$message({ 
                      message: '添加失败，请检查信息重新修改',
                      type: 'error'
                    });
                  }
                })
              }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      Fndelcollectdevice(){//删除采集设备
        url.delcollectdevice({
          cdevice_id:this.formLabelAlign.cdevice_id,
          user_id:this.buserInfo.user_id
        }).then(res=>{
          this.Fncollectdevicelist()//采集列表
          if(res.data.code){
            this.$message({ 
              message: res.data.message[0],
              type: 'success'
            });
          }else{
            this.$message({ 
              message: '删除失败，请检查信息重新修改',
              type: 'error'
            });
          }
        })
      },
      Fnhistory(i){//数据4--列表中操作按钮-历史数据
        console.log('列表中操作按钮-历史数据')
      },
      FnGonSubmit(){//告警5--表单查询事件
        console.log('查询表单提交事件')
      },
      Fnviewproject(){//查看项目信息
        url.viewproject({
          "project_id": this.$route.query.project_id,
          "company_id": this.$route.query.company_id,
          "user_id": this.buserInfo.user_id
        }).then(res=>{
          console.log(res,'------查看项目信息')
          if(res.data.code[0]='200'){
            let xx=res.data.data[0].fields
            xx.project_id=res.data.data[0].pk
            this.ruleForm=xx
          }else{
            this.$message.error('查看项目信息失败')
          }
        })
      },
      Fneditproject(formName){//项目修改
        this.$refs[formName].validate((valid) => {
          if (valid) {
            //   登陆内容中获取的公司——用户
            this.ruleForm.company_id=this.$route.query.company_id
            this.ruleForm.user_id=this.buserInfo.user_id
            url.editproject(this.ruleForm)
            .then(res=>{
              console.log(res,'------修改是否成功')
                if(res.data.code[0]=='200'){
                  this.$message.success(res.data.message[0]);
                }else{
                    this.$message.error('项目修改不成功，请重新创建');
                }
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      Fndeactivateproject(){//删除项目
        url.deactivateproject({
          "project_id": this.$route.query.project_id,
          "company_id": this.$route.query.company_id,
          "user_id": this.buserInfo.user_id
        }).then(res=>{
          if(res.data.code[0]=='200'){
            this.$router.push('/bkIndex/bkprojectManagement')
            this.$message.success('删除成功')
          }else{
            this.$message.error('删除项目不成功')
          }
          console.log(res,'-----------删除项目')
        })
      },
      Fncollectdevicelist(){
        url.collectdevicelist({
          "project_id": this.$route.query.project_id,
          "user_id": this.buserInfo.user_id
        }).then(res=>{
          console.log(res,'-----------列表')
          if(res.data.data.length==0){
            this.ISfromBox=false;
            this.formLabelAlign={}
          }else{
            this.ISfromBox=true;
            let xx=res.data.data[0].fields
            xx.cdevice_id=res.data.data[0].pk
            this.formLabelAlign=xx
          }


        })
      },
    },
    filters:{//告警5-99+的过滤器
      filters2(i){
        if(i>=100){
          return "99+"
        }else{
          return i
        }
      }
    },
    computed:{
      ...mapState(['buserInfo'])
    },
    mounted(){
      this.Fnviewproject()//获取项目信息
      this.Fncollectdevicelist()
    }
  };
</script>

<style lang="stylus" scoped>
  .text {
    font-size: 16px;
  }

  .item {
    margin-bottom: 18px;
    
  }
  .textItemRight
    background #f00
    font-size 14px
    height 13px
    padding 5px
    line-height 13px
    text-align center
    color #fff
    border-radius 50%
    float right
  .textItemRight_bgAll
    background #36c6d3
  .textItemRight_bg1
    background #ed6b75
  .textItemRight_bg2
    background #CC6600
  .textItemRight_bg3
    background #CC9933
  .textItemRight_bg4
    background #F1C40F
  .textItemRight_bg5
    background #659be0
.shezhiL
  margin-top30px
  div
    line-height 40px
    padding-left 20px
    margin 5px 0
  .shezhiLactive
    background #f1f1f1
    border-left 4px solid #409EFF
.box-card
  margin-bottom 25px
  >>>.el-checkbox
    display block
</style>


